// set fonts

html {
	font: $font-style $font-size $font-weight $font-family;
}

h1 {
	font-family: $header-font;
}

h2 {
	font-family: $font-family;
}


// Colours

body {
	background-color: $bg;
	color: $text-col;
}

h1 {
	color: $h1col;
}

h2 {
	color: $subheading;
}

// Basic layout classes

.vertical-center {
	display: flex;
	align-items: center;
	justify-content: center;
}